/**
* dependencias a utilizar
*/
import { Component, OnInit, Input, ElementRef} from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { DomSanitizer, SafeHtml } from "@angular/platform-browser";
import { CrudService } from '../../../crud/crud.service';
import { environment } from 'environments/environment';

import * as jsPDF from 'jspdf';
import * as autoTable from 'jspdf-autotable';
import * as moment from 'moment';


import { forEach } from '@angular/router/src/utils/collection';

/**
* selector si se desea ocupar en un HTML
* y su archivo HTML
*/
@Component({
  selector: 'reporte-incidencia-referencias',
  templateUrl: './lista.component.html'
})

/**
* Esta clase inicializa la lista del componente
* con los datos que se requieran.
*/
export class ReporteReferenciaComponent{

  
  /**
  * Contiene el tamaño del cuerpo de la seccion donde esten los controles en la vista.
  * @type {any}
  */
  tamano = document.body.clientHeight;
  
  /**
  * Contiene array con los valores de la lista
  * respecto a la consulta.
  * @type {any}
  */
  datos: any[] = [];

  /**
  * Contiene la bandera para verificar
  * si se cargo la consulta.
  * @type {boolean}
  */
  cargando: boolean = false;
  
  /**
  * Contiene la bandera los
  * botones de filtrar o imprimir
  * @type {boolean}
  */
  busquedaActivada;

  /**
  * Contiene el objeto JSON repecto
  * a la información de la Unidad Medica.
  * @type {object}
  */
  public clue_login = JSON.parse(localStorage.getItem("clues"));

  /**
  * Contiene el uno de los rangos de fecha
  * fecha de inicio donde se ejecutara la consulta
  * del reporte.
  * @type {object}
  */
  fecha_inicio: any = "";

  /**
  * Contiene el uno de los rangos de fecha
  * fecha de final donde se ejecutara la consulta
  * del reporte.
  * @type {object}
  */
  fecha_fin: any = "";
  
  /**
  * Contiene el valor de la referencia
  * de origen, osease la unidad medica de origen.
  * del reporte.
  * @type {object}
  */
  referencia_origen: any = "";

  /**
  * Contiene el valor de la referencia
  * de destino, osease la unidad medica de destino.
  * del reporte.
  * @type {object}
  */
  referencia_destino: any = "";
  
  /**
  * Contiene el valor temporal de la unidad medica
  * de origen.
  * @type {any}
  */
  clues_temp_origen: any = "";

  /**
  * Contiene el valor temporal de la unidad medica
  * de destino.
  * @type {any}
  */
  clues_temp_destino: any ="";

  /**
  * Contiene la url a consultara
  * @type {any}
  */
  url: any = "";
  
  /**
  * Contiene el autocomplet
  * que busca a las unidades medicas.
  * @type {any}
  */
  public clues_term: string = `${environment.API_URL}/clues-auto?term=:keyword`;

  /**
  * Este método inicializa la carga de las dependencias 
  * que se necesitan para el funcionamiento del catalogo
  */
  constructor(
    private crudService: CrudService,
    private fb: FormBuilder,
    private _sanitizer: DomSanitizer,
    private _el: ElementRef
  ){}

  /**
  * Este método inicializa la carga de la vista asociada junto los datos del formulario
  * @return void
  */
  ngOnInit(){

    moment.locale('es');
    this.fecha_fin = moment().format('YYYY-MM-D h:mm');
    this.fecha_inicio =  moment().subtract(1, 'months').format('YYYY-MM-D h:mm');
    document.getElementById("catalogos").click();

  }

  /**
  * Este método carga los catalogos conectados al servicio
  * donde los parametros se los pasa la lista.
  * @param item elemento donde se almacenara
  * @param url url de la api donde se consulta y se pone el nombre exacto como esta en la api.
  * @return void
  */
  cargarCatalogo(item, url) {
    this.crudService.lista(0, 0, url).subscribe(
      resultado => {
        this[item] = resultado.data;
      },
      error => {

      }
    );
  }

  /**
  * Este método obtiene los datos del servicio a la API
  * a consultar.
  * @return void
  * @param url inicializa la ruta que se manda al servicio para
  * obtener los datos que se requieren.
  */
  listar(url): void {
    
    this.cargando = true;
    this.crudService.lista_general(url).subscribe(
        resultado => {
            this.cargando = false;
            this.datos = resultado as any[];
        },
        error => {

        }
    );
  }

  /**
  * Este método obtiene los datos del servicio a la API
  * a consultar.
  * @return void
  * @param url inicializa la ruta que se manda al servicio para
  * obtener los datos que se requieren de la consulta.
  * @param e el evento que detona el metodo.
  */
  lista_referencias(url, e){
 

    this.listar('reportes/incidencias-referencias?fecha_inicio='+this.fecha_inicio+'&fecha_fin='+this.fecha_fin+'&referencia_origen='+this.referencia_origen+'&referencia_destino='+this.referencia_destino
    );

  }

  /**
  * Este metodo limpia los campos
  * para realizar otra busqueda.
  * @return void
  */
  limpiar_campos_busqueda(){
    this.fecha_inicio = "";
    this.fecha_fin = "";
    this.referencia_origen = "";
    this.referencia_destino = "";
  }

  /**
  * Método para obtener la unidad medica de origen
  * @param modelo nombre del modelo donde se guarda el dato obtenido
  * @param item obtiene el elemnto a extraer.
  * @param datos obtiene los datos
  * @param esmodelo bandera que dermina si es o no modelo
  * @return void
  */
  select_clue_origen_autocomplete(modelo, item, datos, esmodelo: boolean = false) {
    if (!esmodelo)
      modelo = datos[item];
      this.referencia_origen = datos[item];
  }

  /**
  * Método para obtener la unidad medica de destino
  * @param modelo nombre del modelo donde se guarda el dato obtenido
  * @param item obtiene el elemnto a extraer.
  * @param datos obtiene los datos
  * @param esmodelo bandera que dermina si es o no modelo
  * @return void
  */
  select_clue_destino_autocomplete(modelo, item, datos, esmodelo: boolean = false) {
    if (!esmodelo)
      modelo = datos[item];
      this.referencia_destino = datos[item];
  }
  
  /**
  * Método para lista la unidad medica de origen en el Autocomplet
  * @param data contiene los elementos que se escriban en el input del Autocomplet
  */
  autocompleOrigenFormatter = (data: any) => {

    let html = `<span>(${data.clues}) - ${data.nombre} </span>`;
    return this._sanitizer.bypassSecurityTrustHtml(html);

  }
  /**
  * Método para obtener el valor de la Unidad Medica de origen
  * @param data contiene el valor de la Unidad Medica de origen
  * @return void
  */
  valorFormato_clueOrigen(data: any,) {

      let html = `${data.nombre}`;
      return html;     
  }

  /**
  * Método para lista la unidad medica de destino en el Autocomplet
  * @param data contiene los elementos que se escriban en el input del Autocomplet
  */
  autocompleDestinoFormatter = (data: any) => {
    
    let html = `<span>(${data.clues}) - ${data.nombre} </span>`;
    return this._sanitizer.bypassSecurityTrustHtml(html);
  }
  
  /**
  * Método para obtener el valor de la Unidad Medica de destino
  * @param data contiene el valor de la Unidad Medica de destino
  * @return void
  */  
  valorFormato_clueDestino(data: any,) {

      let html = `${data.nombre}`;
      return html;     
  }
  /**
  * Este metodo indica si una referencia
  * es de ingreso.
  * @return void
  */
  esIngreso(){
    this.referencia_destino = this.clue_login.clues;
    this.clues_temp_destino = this.clue_login.nombre;

    this.referencia_origen = '';
    this.clues_temp_origen = '';
  }
  
  /**
  * Este metodo indica si una referencia
  * es de envio.
  * @return void
  */
  esEnvio(){

    this.referencia_origen = this.clue_login.clues;
    this.clues_temp_origen = this.clue_login.nombre;

    this.referencia_destino = '';
    this.clues_temp_destino = '';    


  }
  
  /**
  * Este metodo genera el pdf con la consulta del HTML
  * en la vista y con ayuda de la libreria jspdf
  * @return void
  */
  generate() {
    
    let jsPDF = require('jspdf');
    require('jspdf-autotable');

  

      var doc = new jsPDF('l');

      var img = {
        header: 'data:image/jpeg;base64,'
      }

      var fecha_actual =  moment().format('D/MM/YYYY h:mm');
      
      var clue = this.clue_login;
  
      var res = doc.autoTableHtmlToJson(document.getElementById("datosReporte"));
    
      var totalPagesExp = "{total_pages_count_string}";

      var pageContent = function (data) {
        // HEADER
        doc.setFontSize(20);
        doc.setTextColor(40);
        doc.setFontStyle('normal');

        if (img.header ) {
          doc.addImage(img.header, 'JPEG', 30, 12, 240, 18);
          // doc.addImage(img.base64Img2, 'JPEG', 10, 10, 60, 20);
          // doc.addImage(img.base64Img3, 'JPEG', 97, 3, 15, 15);
          // doc.addImage(img.base64Img3, 'JPEG', 194, 3, 15, 15);
          
        }

        doc.text("Reporte de Referencias", data.settings.margin.left + 100, 9);
        doc.setFontSize(8)
        doc.setFont('helvetica')
        doc.text("Sistema para la Gestión de Incidencias en Salud (SGiS)", data.settings.margin.left + 7, 13);
        doc.setFontSize(8)
        doc.setFont('helvetica')
        doc.text('('+clue.clues+')'+' '+clue.nombre, data.settings.margin.left + 85, 13);

        // FOOTER
        var str = "Pagina " + data.pageCount;
        // Total page number plugin only available in jspdf v1.0+
        if (typeof doc.putTotalPages === 'function') {
            str = str + " de " + totalPagesExp;
        }
        doc.setFontSize(10);
        doc.setFontType("italic");
        doc.text(str, data.settings.margin.left, doc.internal.pageSize.height - 10);
    };
    


      doc.autoTable(res.columns, res.data, {

        startY: 31,
        margin: {
          top: 30,
          horizontal: 7
        },
        styles: {
          overflow: 'linebreak',
          fontSize: 10,
          tableWidth: 'auto',
          columnWidth: 'auto',
        },
        columnStyles: {
          0: {columnWidth: 10},
          1: {columnWidth: 40},
          2: {columnWidth: 40},
          3: {columnWidth: 50},
          4: {columnWidth: 70},
          5: {columnWidth: 70},
        },
        addPageContent: pageContent
      });

      if (typeof doc.putTotalPages === 'function') {
        doc.putTotalPages(totalPagesExp);
      }

    
      doc.save('Reporte de Referencias'+' '+fecha_actual);
  
   }
  }


